.banner {
  display: flex;
  margin-top: 2%;
  justify-content: space-between;
  align-items: center;
  /* border: 1px solid black; */
  min-height: 80vh;
}
.pic {
  height: 350px;
}

.about-left {
  display: flex;
  width: 50%;

  /* border: 1px solid red; */
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
}
.about-left h1 {
  font-size: 58px;
  color: #110f0a;
  margin-bottom: 0;
}
.about-left p {
  font-size: 24px;
  color: #110f0a;
}
.btn {
  width: 125px;
  padding: 10px;
  cursor: pointer;
  background: #e0c16f;
  font-size: 20px;
  border: none;
  color: white;
}
@media only screen and (max-width: 720px) {
  .banner {
    margin-top: 0%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .about-left {
    width: 100%;
  }
  .pic {
    height: 150px;
    width: 100%;
  }
  .about-left h1 {
    font-size: 34px;
  }
  .about-left p {
    font-size: 20px;
  }
}
