.services {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* border: 1px solid red; */
}
.title {
  text-align: center;
}
.title h1 {
  font-size: 58px;
  color: #110f0a;
  margin-bottom: 0;
}
.title p {
  font-size: 24px;
  color: #110f0a;
  /* color: red; */
}
.samples {
  /* border: 1px solid green; */
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
}
.samplePic {
  border-radius: 24px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  /* width: 400px;/ */
  height: 400px;
  width: 98%;
}
.imgCard {
  width: 33%;
}
@media only screen and (max-width: 720px) {
  .samples {
    flex-direction: column;
  }
  .imgCard {
    width: 100%;
  }
  .title h1 {
    font-size: 34px;
  }
  .title p {
    font-size: 20px;
  }
}
