.about {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about-left {
  width: 50%;
}
.about-left h1 {
  font-size: 58px;
  color: #110f0a;
}

@media only screen and (max-width: 720px) {
  .about {
    margin-top: 0%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .pic {
    height: 290px;
  }
  .about-left {
    width: 100%;
  }
  .about-left h1 {
    font-size: 34px;
  }
  .about-left p {
    font-size: 20px;
  }
}
