.news {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* border: 2px solid red; */
}
.news_head {
  text-align: center;
}
.news_head h1 {
  font-size: 58px;
  font-weight: 600;
  color: #110f0a;
  margin-bottom: 0;
}
.news_head h2 {
  font-size: 24px;
  font-weight: 400;
  color: #110f0a;
}
.news_info h2 {
  font-weight: 400;
  color: #e0c16f;
}
.news_info p {
  font-size: 20px;
  color: grey;
}
.content {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.content_left {
  width: 50%;
}

.content_right {
  width: 50%;
}
@media only screen and (max-width: 720px) {
  .news_head h1 {
    font-size: 34px;
  }
  .content {
    flex-direction: column;
  }
  .content_left {
    /* border: 1px solid red; */
    width: 100%;
    text-align: center;
  }
  .content_left img {
    height: 225px;
  }
  .content_right {
    width: 100%;
  }
}
